.girf-card {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }

  .container {
    @media screen and (max-width: $tablet-small - 1){
      padding-left: 0;
      padding-right: 0;
    }
    @media screen and (min-width: $desktop-small){
      display: flex;
      flex-wrap: wrap;
    }
  }

  .image {
    @media screen and (min-width: $desktop-small){
      width: calc(60% - 10px);
      margin-right: 20px;
      img {
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 40px 20px;
    background-color: var(--blue);
    color: var(--gray);
    border-radius: 3px;
    @media screen and (min-width: $tablet-medium){
      padding: 50px;
    }
    @media screen and (min-width: $desktop-small){
      width: calc(40% - 10px);
    }

    .top-title {
      margin-bottom: 15px;
    }

    .description {
      margin-bottom: 24px;
      @media screen and (min-width: $tablet-medium){
        margin-bottom: 30px;
      }
    }

    .btn {
      align-self: center;
    }
  }
}
