.shop-introduce {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }

  .list-slider {
    .slick-track {
      display: flex;
    }
    @media screen and (min-width: $desktop-small){
      display: flex;
      .slick-list {
        width: 100%;
      }
      .slick-track {
        width: 100%!important;
      }
    }
    @media screen and (max-width: $tablet-medium - 1){
      margin-right: -15px;
      .slick-list {
        padding-right: 35px;
      }
    }
  }

  &__item {
    display: flex;
    align-items: center;
    @include backgroundSize(cover);
    min-height: 175px;
    margin-bottom: 20px;
    margin-right: 15px;
    height: auto;
    @media screen and (min-width: $tablet-medium){
      min-height: 320px;
      margin-right: 0;
    }
    @media screen and (min-width: $desktop-small){
      width: calc(50% - 10px)!important;
      margin-right: 20px;
      margin-bottom: 0;
      min-height: 350px;
    }

    &:last-child {
      margin-right: 0;
    }

    .content {
      padding: 50px 20px;
      @media screen and (min-width: $desktop-medium){
        width: calc(100% - 300px);
        padding: 50px 32px 50px 50px;
      }
      @media screen and (max-width: $tablet-medium - 1){
        max-width: 240px;
      }

      .category {
        font-size: 12px;
        line-height: 15px;
        color: var(--gray);
        letter-spacing: 0.2em;
        text-transform: uppercase;
        margin-bottom: 9px;
        @media screen and (min-width: $tablet-medium){
          font-size: 14px;
          line-height: 17px;
        }
      }

      .title {
        font-weight: bold;
        margin-bottom: 15px;
      }
    }
  }
}
