.product-search {
  padding-bottom: 40px;
}

.product-filter {
  padding-bottom: 20px;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--line);
  @media screen and (min-width: 768px){
    display: flex;
    justify-content: space-between;
  }
  .list-control {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    @media screen and (min-width: 768px){
      justify-content: flex-start;
      margin-top: 0;
      li {
        &::after {
          content: '';
          position: absolute;
          top: 2px;
          right: 0;
          height: 21px;
          width: 1px;
          background-color: var(--line-2);
        }
      }
    }
    li {
      position: relative;
      @media screen and (min-width: 768px){
        padding: 0 20px;
      }
      &:last-child {
        padding-right: 0;
        &::after {
          display: none;
        }
      }
      i {
        margin-right: 10px;
      }
    }
    .action-view {
      @media screen and (max-width: 767px){
        flex: 0 0 100%;
        width: 100%;
        margin-top: 20px;
      }
    }
  }

  .checkbox {
    margin-top: 20px;
    input {
      display: none;
      &:checked {
        + label {
          &::after {
            opacity: 1;
          }
        }
      }
    }
    label {
      position: relative;
      padding-left: 35px;
      cursor: pointer;
      &::before,&::after {
        content: '';
        position: absolute;
      }
      &::before {
        left: 0;
        top:-5px;
        background: var(--white);
        border: 1px solid #D7D7D7;
        border-radius: 3px;
        height: 24px;
        width: 24px;
      }
      &::after {
        top: 1px;
        left: 6px;
        border-radius: 3px;
        height: 12px;
        width: 12px;
        background-color: var(--dark);
        opacity: 0;
        transition: all .3s;
      }
    }
  }
}

.top-search {
  text-align: center;
  margin-bottom: 40px;

  .title {
    margin-bottom: 20px;
  }

  .form-search {
    .form-group {
      position: relative;
      max-width: 700px;
      margin: 0 auto;
    }

    .btn-search {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }

    .form-control {
      border: 1px solid var(--line-2);
      border-radius: 35px;
      padding: 10px 40px 10px 20px;
      height: 50px;
      font-size: 18px;
      line-height: 18px;
      width: 100%;
      &:focus, &:hover {
        border-color: var(--dark);
      }
      @include placeholder {
        color: var(--l-gray);
        font-weight: 400;
      }
    }
  }
}
