.product {
  &__item {

    .image {
      position: relative;
      margin-bottom: 10px;
      overflow: hidden;
      height: 175px;
      max-height: 380px;
      border: 1px solid transparent;
      border-radius: 3px;
      @media screen and (min-width: $tablet-medium){
        height: 380px;
      }
      @media screen and (min-width: $desktop-small) {
        height: 470px;
        max-height: 470px;
      }

      img {
        @include positionFull();
        object-fit: cover;
      }
    }

    .add-to-card {
      position: absolute;
      right: 15px;
      bottom: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
      background-color: var(--white);
      transition: all .3s ease;
      cursor: pointer;
      @media screen and (min-width: $tablet-medium){
        width: 40px;
        height: 40px;
      }

      &:hover {
        background-color: var(--dark);
        color: var(--white);
      }

      .tooltip__content {
        background-color: var(--white);
        color: var(--dark);
        min-width: 75px;
        &::after {
          border-left-color: var(--white);
        }
      }

      &::after {
        content: '\e902';
        display: block;
        @include e-comstore;
      }
    }

    .sale {
      position: absolute;
      left: 15px;
      top: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: var(--white);
      background-color: var(--dark);
      border-radius: 40px;
      height: 25px;
      width: 60px;
      z-index: 1;
    }

    .info {
      font-size: 14px;
      line-height: 18px;
      text-align: center;
      @media screen and (min-width: $tablet-medium){
        font-size: 18px;
        line-height: 20px;
      }
    }

    .title {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 5px;
      @media screen and (min-width: $tablet-medium){
        font-size: 18px;
        margin-bottom: 10px;
      }

      a {
        position: relative;
        padding-bottom: 2px;

        &:hover {
          &::after {
            right: 0;
          }
        }

        &::after {
          content: '';
          position: absolute;
          left: 0;
          right: 100%;
          bottom: 0;
          height: 1px;
          background-color: var(--dark);
          transition: all .3s ease;
        }
      }
    }

    .old-price {
      font-size: 12px;
      line-height: 15px;
      color: var(--l-gray);
      margin-left: 5px;
      text-decoration: line-through;
      @media screen and (min-width: $tablet-medium){
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
}

