@font-face {
  font-family: 'e-comstore';
  src: url('../fonts/icomoon/icomoon.eot?#{$random}');
  src: url('../fonts/icomoon/icomoon.woff2?#{$random}') format('woff2'), // sass-lint:disable-line no-duplicate-properties
  url('../fonts/icomoon/icomoon.woff?#{$random}') format('woff'),
  url('../fonts/icomoon/icomoon.ttf?#{$random}') format('truetype'),
  url('../fonts/icomoon/icomoon.svg?#{$random}#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^='icon-'], [class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'e-comstore' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

