.checkbox-dropdown {
  position: relative;
  max-width: 175px;

  &.price-dropdown {
    .checkbox-dropdown__label {
      .count {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--gray);
        border-radius: 50%;
        &::before {
          content: '';
          display: block;
          width: 7px;
          height: 7px;
          background-color: var(--gray);
          border-radius: 50%;
        }
      }
    }
    .checkbox-dropdown__list {
      @media screen and (min-width: $tablet-medium){
        min-width: 365px;
      }
    }
  }

  &.active & {
    &__label {

      .count {
        font-size: 12px;
        color: var(--white);

        &::after {
          opacity: 1;
        }
      }
    }
  }

  &.open & {
    &__list {
      display: block;
    }

    &__label {
      border-color: var(--gray-2);
      &::before {
        opacity: 1;
        transform: rotate(180deg);
      }
    }
  }

  &__label {
    position: relative;
    font-size: 16px;
    line-height: 16px;
    background: #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--gray);
    border: 1px solid var(--l-gray);
    box-sizing: border-box;
    border-radius: 3px;
    padding: 12px 40px 12px 15px;
    &::before {
      content: '\e904';
      position: absolute;
      top: 12px;
      right: 10px;
      font-size: 20px;
      @include e-comstore;
      transition: transform .3s ease;
    }

    .count {
      position: relative;
      width: 19px;
      height: 19px;
      font-size: 0;
      line-height: 19px;
      color: var(--dark);
      font-weight: 500;
      text-align: center;
      margin-left: 10px;
      z-index: 1;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: var(--gray);
        opacity: 0;
        z-index: -1;
      }
    }
  }

  &__list {
    min-width: 270px;
    background: #fff;
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    right: 0;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.07), 0 20px 30px rgba(0, 0, 0, 0.07);
    display: none;
    padding: 20px;
    z-index: 10;

    .checkbox-list {
      max-height: 380px;
      overflow-y: auto;
    }
  }

  .checkbox {
    &__input {
      display: none;

      &:checked + label {
        &:before {
          border-color: var(--dark);
        }
        &::after {
          opacity: 1;
        }
      }

      &:disabled + label {
        color: var(--gray);
      }
    }

    &__label {
      position: relative;
      cursor: pointer;
      display: inline-block;
      margin-bottom: 10px;

      &:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        text-align: center;
        width: 18px;
        height: 18px;
        border: solid 1px var(--l-gray);
        background-color: var(--white);
        border-radius: 2px;
        margin-top: -3px;
      }

      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 6px;
        width: 6px;
        height: 10px;
        border: solid var(--dark);
        border-width: 0 1px 1px 0;
        transform: rotate(45deg);
        opacity: 0;
      }
    }
  }
}
