.sign-up {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }
  &__box {
    display: flex;
    align-items: center;
    padding: 50px 0;
    min-height: 300px;
    @include backgroundSize(cover);
    @media screen and (min-width: $desktop-small){
      min-height: 395px;
    }
    @media screen and (max-width: $desktop-small - 1){
      background-position: 75%;
    }
  }
  .content {
    text-align: center;
    max-width: 250px;
    margin: 0 auto;
    color: var(--gray);
    @media screen and (min-width: $tablet-medium){
      max-width: 480px;
    }
    @media screen and (min-width: $desktop-small){
      max-width: 633px;
    }

    .title {
      font-weight: 600;
      color: var(--dark);
      margin-bottom: 10px;
    }

    .btn {
      margin-top: 35px;
    }
  }
}
