/* Mixin placeholder */
@mixin placeholder {
  @at-root #{&}::-webkit-input-placeholder {
    @content
  }
  @at-root #{&}:-moz-placeholder {
    @content
  }
  @at-root #{&}::-moz-placeholder {
    @content
  }
  @at-root #{&}:-ms-input-placeholder {
    @content
  }
  @at-root #{&}::placeholder {
    @content
  }
}

/* Mixin media query */
@mixin respond-to($breakpoint) {
  @media only screen and (max-width: $breakpoint) {
    @content;
  }
}
@mixin respond-from($breakpoint) {
  @media only screen and (min-width: $breakpoint) {
    @content;
  }
}

/* Mixin font icons */
@mixin e-comstore {
  font-family: 'e-comstore' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

/* Mixin background */
@mixin backgroundSize($size) {
  background-position: center;
  background-repeat: no-repeat;
  background-size: $size;
}

/* Mixin position */
@mixin positionFull() {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

@mixin positionAll($top, $right, $bottom, $left) {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

@mixin positionCenterMiddle() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin positionLeftMiddle() {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

@mixin positionRightMiddle() {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

@mixin magic-border($width, $color, $duration, $direction){
  position:relative;
  &:before{
    content:'';
    position:absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background:linear-gradient(to right, $color 0%, $color 100%), linear-gradient(to top, $color 50%, transparent 50%), linear-gradient(to top, $color 50%, transparent 50%), linear-gradient(to right, $color 0%, $color 100%), linear-gradient(to left, $color 0%, $color 100%);
    background-size:100% $width, $width 200%, $width 200%, 0% $width, 0% $width;
    background-position:50% 100%, 0% 0%, 100% 0%, 100% 0%, 0% 0%;
    background-repeat:no-repeat, no-repeat;
    transition:transform $duration ease-in-out, background-position $duration ease-in-out, background-size $duration ease-in-out;
    transform:scaleX(0) rotate(180deg * $direction);
    transition-delay:$duration*2, $duration, 0s;
    z-index: 1;
  }
  &:hover{
    &:before{
      background-size:200% $width, $width 400%, $width 400%, 55% $width, 55% $width;
      background-position:50% 100%, 0% 100%, 100% 100%, 100% 0%, 0% 0%;
      transform:scaleX(1) rotate(180deg * $direction);
      transition-delay:0s, $duration, $duration*2;
    }
  }
}

// Animation placeholder loader
@keyframes animateLoader{
  0%{
    background-position: -468px 0;
  }
  100%{
    background-position: 468px 0;
  }
}

@mixin animated-background {
  animation-duration: 1.25s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: animateLoader;
  animation-timing-function: linear;
  background-size: 800px 104px;
  position: relative;
}
