#main-content {
  padding-top: 60px;
  @media screen and (min-width: $desktop-small){
    padding-top: 134px;
  }
}

.row {
  @media screen and (min-width: $tablet-medium) {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
}

[class*='col-'] {
  @media screen and (min-width: $tablet-medium) {
    width: 100%;
    position: relative;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (min-width: $tablet-medium) {
  @for $i from 1 through 12 {
    .col-#{$i} {
      width: 100% / 12 * $i;
    }
  }
}
