.tooltip {
  position: relative;

  &--center {
    &:hover {
      .tooltip__content {
        top: calc(100% + 10px);
      }
    }

    .tooltip__content {
      top: 110%;
      left: 50%;
      transform: translateX(-50%);

      &::after {
        top: -5px;
        left: 50%;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-bottom: 8px solid var(--dark);
        transform: translateX(-50%);
      }
    }
  }

  &--left {
    &:hover {
      .tooltip__content {
        right: calc(100% + 10px);
      }
    }

    .tooltip__content {
      right: calc(120% + 10px);
      top: 50%;
      transform: translateY(-50%);

      &::after {
        right: -5px;
        top: 50%;
        border-top: 8px solid transparent;
        border-left: 8px solid var(--dark);
        border-bottom: 8px solid transparent;
        transform: translateY(-50%);
      }
    }
  }

  &:hover {
    .tooltip__content {
      visibility: visible;
      opacity: 1;
    }
  }

  &__content {
    visibility: hidden;
    position: absolute;
    display: block;
    z-index: 20;
    padding: 4px 7px;
    min-width: max-content;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
    text-align: center;
    opacity: 0;
    transition-property: all;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    transition-duration: .3s;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.07);
    background-color: var(--dark);
    color: var(--white);
    border-radius: 2px;

    &::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      z-index: -1;
    }
  }
}
