.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0 40px;

  .breadcrumb-item {
    font-size: 14px;
    line-height: 16px;
    color: var(--gray);
    &.active, &:hover {
      color: var(--dark);
    }
    + .breadcrumb-item {
      &::before {
        display: inline-block;
        padding-right: .5rem;
        padding-left: .5rem;
        color: var(--gray);
        content: '/';
      }
    }
  }
}


