.e-dropdown {
  position: relative;
  padding-right: 15px;
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }

  &.show {
    &::before {
      transform: translateY(-50%) rotate(180deg);
    }

    .dropdown {
      transform: translate(-50%, 0);
      pointer-events: auto;
      opacity: 1;
    }
  }

  &::before {
    content: '\e904';
    @include e-comstore;
    position: absolute;
    top: 50%;
    right: 0;
    transition: all .3s;
    transform: translateY(-50%);
  }

  .dropdown-toggle {
    display: block;
    padding: 10px 0;
    @media screen and (min-width: $desktop-small) {
      padding: 16px 0;
    }
  }

  .dropdown {
    position: absolute;
    min-width: max-content;
    left: 50%;
    top: 100%;
    padding: 5px 0;
    background-color: var(--white);
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.07), 0 20px 30px rgba(0, 0, 0, 0.07);
    opacity: 0;
    pointer-events: none;
    transform: translate(-50%, 10px);
    transition: .3s ease-in-out;
    z-index: 1050;

    li {
      text-align: center;
      padding: 8px 15px;
      @media screen and (min-width: $desktop-small) {
        &.selected, &:hover {
          a::before {
            right: 0;
          }
        }
      }
    }

    a {
      position: relative;
      color: var(--dark);
      font-size: 16px;
      @media screen and (min-width: $desktop-small) {
        &::before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          right: 100%;
          height: 1px;
          background-color: var(--dark);
          transition: all .2s ease-in-out;
        }
      }
    }
  }
}

.qty-input {
  display: flex;
  width: 120px;
  height: 35px;
  padding: 0;
  cursor: pointer;
  border: 1px solid var(--line);

  .product-qty, .qty-count {
    background: transparent;
    color: inherit;
    font-size: inherit;
    border: 0;
  }

  .product-qty {
    width: 50px;
    padding: 5px;
    color: var(--gray);
    text-align: center;
    /* Chrome, Safari, Edge, Opera */
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    /* Firefox */
    &[type=number] {
      -moz-appearance: textfield;
    }
  }

  .qty-count {
    position: relative;
    overflow: hidden;
    padding: 0;
    cursor: pointer;
    width: 35px;
    color: var(--l-gray);
    transition: all .3s;

    &:hover {
      color: var(--dark);
    }

    &::before {
      @include e-comstore;
      display: block;
    }

    &--minus {
      box-shadow: 1px 0 0 var(--line);
      border-radius: 0 0 2px 2px;

      &::before {
        content: '\e901';
      }
    }

    &--add {
      box-shadow: -1px 0 0 var(--line);
      border-radius: 2px 2px 0 0;

      &::before {
        content: '\e902';
      }
    }
  }
}

.select-custom {
  position: relative;
  margin-bottom: 15px;

  label {
    position: absolute;
    left: 15px;
    top: 10px;
    opacity: 0;
    font-size: 14px;
    line-height: 14px;
    color: var(--gray);
  }

  .select2-container--select-md {
    width: 100% !important;
  }

  .select2-container--select-md .select2-selection--single .select2-selection__rendered {
    line-height: 28px;
    padding: 15px;
  }

  &.selected {
    .select2-container--select-md .select2-selection--single .select2-selection__rendered {
      padding-top: 24px;
      padding-bottom: 6px;
      color: var(--dark);
    }

    label {
      opacity: 1;
    }
  }
}

.select2-container {
  font-size: 14px;
  line-height: 24px;

  .select2-search--dropdown {
    padding: 20px 15px 0;

    .select2-search__field {
      border: 1px solid var(--l-gray);
      border-radius: 3px;
      padding: 10px;
      &:hover, &:focus {
        border-color: var(--dark);
      }
      @include placeholder {
        color: var(--line-2);
      }
    }
  }

  .select2-results__options {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    padding: 10px 13px;
    max-height: 370px;
    overflow-y: auto;

    .mCustomScrollBox {
      width: 100%;
      .mCSB_container {
        display: flex;
        align-items: baseline;
        flex-direction: column;
      }
      li {
        display: inline-block;
      }
    }
  }

  .select2-results__option {
    position: relative;
    padding: 0;
    margin-bottom: 10px;

    &--highlighted.select2-results__option--selectable {
      background-color: var(--white);
      color: var(--dark);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 100%;
      bottom: 0;
      height: 1px;
      background-color: var(--dark);
      transition: all .3s ease;
    }

    &--selected {
      background-color: var(--white);
    }

    &--selected, &:hover {
      background-color: var(--white);
      color: var(--dark);

      &::after {
        right: 0;
      }
    }
  }

  .select2-dropdown {
    min-width: max-content;
    border: 0;
    border-radius: 0;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.07), 0 20px 30px rgba(0, 0, 0, 0.07);
  }

  &--open {
    .select2-selection__arrow {
      &::before {
        transform: rotate(180deg);
      }
    }

    .select2-selection--single {
      border-color: var(--dark) !important;
    }
  }

  .select2-selection--single {
    height: auto;
    border: 1px solid var(--l-gray);
    border-radius: 3px;

    .select2-selection__arrow {
      position: absolute;
      right: 5px;
      display: flex;
      align-items: center;
      top: 0;
      height: 100%;
      width: 20px;

      &::before {
        content: '\e904';
        @include e-comstore;
        transition: all .3s ease;
      }

      b {
        display: none;
      }
    }
  }

  .select2-selection__rendered {
    font-size: 14px;
    line-height: 24px;
    padding: 5px 13px;
    color: var(--dark);
  }

  &--select-md, &--select-text {
    font-size: 16px;

    .select2-results__options {
      padding: 20px 15px;
    }

    .select2-results__option {
      margin-bottom: 15px;
    }

    .select2-selection--single {
      .select2-selection__arrow {
        font-size: 20px;
      }

      .select2-selection__rendered {
        font-size: 16px;
        line-height: 29px;
        color: var(--gray);
      }
    }
  }

  &--select-md {
    width: 225px !important;

    .select2-selection--single {
      .select2-selection__arrow {
        width: 30px;
      }

      .select2-selection__rendered {
        padding: 7px 20px;
      }
    }
  }

  &--select-text {
    .select2-dropdown {
      margin-left: -25px;
    }

    .select2-selection--single {
      display: flex;
      align-items: center;
      border: 0;

      .select2-selection__arrow {
        position: static;
        padding-left: 10px;
      }

      .select2-selection__rendered {
        color: var(--dark);
        font-weight: 500;
        padding: 0;
      }
    }
  }
}

.form-ani {
  position: relative;
  margin-bottom: 15px;

  &.focused label {
    top: 10px;
    font-size: 14px;
    line-height: 14px;
  }

  label {
    position: absolute;
    left: 15px;
    top: 18px;
    color: var(--gray);
    transition: all 150ms ease-out;
    z-index: 0;
  }

  .form-control {
    position: relative;
    padding: 30px 15px 10px;
    z-index: 1;
  }
}

.form-control {
  border: 1px solid var(--line-2);
  max-width: 100%;
  width: 100%;
  border-radius: 5px;
  padding: 15px;
  resize: none;
}

.row-form {
  @media screen and (min-width: $tablet-medium) {
    display: flex;
    flex-direction: row;
    .form-price {
      width: calc(50% - 7px);
      margin-right: 15px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.form-price {
  position: relative;

  &::after {
    content: '$';
    position: absolute;
    color: var(--gray);
    right: 10px;
    bottom: 10px;
  }

  label {
    font-size: 14px;
    line-height: 14px;
    color: var(--gray);
  }

  .form-control {
    border-radius: 3px;
    padding: 12px 10px;
  }
}

.radio {
  margin-right: 30px;
  @media screen and (max-width: $tablet-medium - 1) {
    margin-bottom: 15px;
  }

  input {
    display: none;

    &:checked + label {
      &::before {
        border-color: var(--dark);
      }

      &::after {
        opacity: 1;
      }
    }
  }

  label {
    position: relative;
    padding-left: 34px;
    cursor: pointer;

    &::before, &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      transition: all .3s ease;
    }

    &::before {
      left: 0;
      top: -4px;
      width: 24px;
      height: 24px;
      border: 1px solid var(--line-2);
    }

    &::after {
      left: 6px;
      top: 2px;
      width: 12px;
      height: 12px;
      background-color: var(--dark);
      opacity: 0;
    }
  }
}

.ui-slider {
  &.ui-slider-horizontal {
    height: 4px;
    border: 0;
    border-radius: 0;
    background-color: var(--line);
  }

  .ui-widget-header {
    background-color: var(--dark);
    border-radius: 0;
  }

  .ui-slider-handle {
    width: 12px;
    height: 12px;
    background-color: var(--white);
    border-radius: 50%;
    margin-left: -2px;
    cursor: pointer;
    transition: all .3s ease;
    border: 0;
    &.ui-state-active {
      background-color: var(--dark);
    }
    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      top: 0;
      left: 0;
      border: 3px solid var(--dark);
    }
    &:hover, &.ui-state-active {
      transform: scale(1.5);
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
      &::before {
        transform: none;
        border: 2px solid var(--dark);
      }
    }
  }
}

.input-range{
  margin-top: 10px;
  display: flex;
  font-size: 14px;
  line-height: 17px;
  color: var(--gray);
  label {
    flex-shrink: 0;
    margin-right: 5px;
  }
}
