.product-category {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 120px;
  }
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  &.grid {
    .product__item {
      position: relative;
      width: 100%;
      margin-bottom: 30px;

      @media screen and (min-width: $tablet-medium){
        width: calc(33.33% - 13.33px);
        margin-right: 20px;
        margin-bottom: 45px;
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
      @media screen and (min-width: $desktop-small){
        width: calc(25% - 15px);
        &:nth-child(3n) {
          margin-right: 20px;
        }
        &:nth-child(4n) {
          margin-right: 0;
        }
      }
    }
  }

  &.list {
    .product__item {
      display: flex;
      align-items: center;
      width: 100%;
      .image {
        width: 355px;
        margin-right: 30px;
      }
    }
  }

}

.action-filter {
  display: flex;
  align-items: center;
  li {
    position: relative;
    color: var(--dark);
    label {
      color: var(--gray);
    }
    i {
      margin-right: 7px;
    }
    &:first-child {
      padding-right: 20px;
      margin-right: 20px;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        height: 21px;
        width: 1px;
        background-color: var(--line-2);
      }
    }
  }
}

.loaded {
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--l-gray);
}
