.news {
  &__item {
    .image {
      border-radius: 3px;
      margin-bottom: 18px;
    }
    .date {
      font-size: 14px;
      line-height: 14px;
      color: var(--gray);
      margin-bottom: 12px;
    }
    .title {
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: var(--dark);
      @media screen and (min-width: $desktop-small){
        font-size: 24px;
        line-height: 32px;
      }
    }
  }
}
