html {
  &.open-cart {
    overflow: hidden;

    .view-cart {
      pointer-events: auto;

      .float-view__overlay {
        opacity: 1;
      }

      .float-view__content {
        width: 85%;
        @media screen and (min-width: $tablet-medium) {
          width: 470px;
        }
      }
    }
  }

  &.open-filter {
    overflow: hidden;

    .float-filter {
      pointer-events: auto;

      .float-view__overlay {
        opacity: 1;
      }

      .float-view__content {
        width: 85%;
        @media screen and (min-width: $tablet-medium) {
          width: 470px;
        }
      }
    }
  }
}

.float-view {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  height: 100%;
  display: flex;
  pointer-events: none;
  z-index: 9999;
  &.float-filter {
    .item-category {
      margin-bottom: 25px;
    }
    .label-cat {
      display: inline-block;
      letter-spacing: 0.1em;
      color: var(--gray);
      margin-bottom: 10px;
    }
    .list-opt {
      li {
        margin-bottom: 10px;
      }
    }
    .list-color li {
      width: 32px;
      height: 32px;
    }
    .list-size li {
      height: 40px;
      width: 95px;
      text-transform: inherit;
    }

    #slider-range {
      margin-top: 10px;
    }
  }

  &.view-cart {
    .float-view__body {
      max-height: calc(100vh - 300px);
      @media screen and (min-width: $tablet-medium) {
        max-height: calc(100vh - 260px);
      }

      .shopping-item {
        display: flex;
        padding: 20px 0;

        .image {
          max-width: 60px;
          flex-shrink: 0;
          margin-right: 15px;
          @media screen and (min-width: $tablet-medium) {
            max-width: 105px;
          }
        }

        .btn-remove {
          svg:hover {
            path {
              stroke: var(--dark);
            }
          }
        }
      }

      .product-name {
        margin-bottom: 5px;
      }

      .product-price {
        color: var(--dark);
        margin-bottom: 20px;

        .old-price {
          color: var(--l-gray);
        }
      }

      .product-action {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .qty-input {
          margin-left: 0;
        }
      }
    }

    .float-view__footer {
      padding: 20px 15px;
      box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.1);
      @media screen and (min-width: $tablet-medium) {
        padding: 25px 30px;
      }

      .text-discount {
        font-size: 14px;
        line-height: 20px;
        color: var(--gray);
        margin-bottom: 10px;
      }

      .sub-total {
        display: flex;
        justify-content: space-between;
        color: var(--dark);
        margin-bottom: 15px;
        @media screen and (min-width: $tablet-medium){
          margin-bottom: 25px;
        }

        .text {
          font-size: 14px;
          line-height: 14px;
          letter-spacing: 0.1em;
          text-transform: uppercase;
        }
      }
    }
  }

  &__content {
    background-color: var(--white);
    width: 0;
    transition: all .3s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 3;

    .wrap {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
      min-width: 300px;
      @media screen and (min-width: $tablet-medium) {
        width: 470px;
      }
    }
  }

  &__overlay {
    margin-left: auto;
    background: rgba(0, 0, 0, 0.7);
    width: 15%;
    opacity: 0;
    z-index: 2;
    @media screen and (min-width: $tablet-medium) {
      width: 100%;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding: 24px 15px 15px;
    border-bottom: 1px solid var(--line);
    @media screen and (min-width: $tablet-medium) {
      padding: 24px 30px 20px;
    }

    .title {
      display: flex;
      align-items: center;
      font-weight: bold;
      font-size: 24px;
      line-height: 24px;

      .badge {
        font-size: 14px;
        line-height: 14px;
        font-weight: 400;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 22px;
        background: var(--line);
        border-radius: 30px;
        margin-left: 10px;
      }
    }

    .close {
      font-size: 22px;
      color: var(--gray);
      cursor: pointer;
    }
  }

  &__body {
    max-height: calc(100vh - 100px);
    overflow-y: auto;
    padding: 15px;
    @media screen and (min-width: $tablet-medium) {
      padding: 25px 30px;
    }
  }

  &__footer {
    margin-top: auto;
    padding: 20px 15px;
    box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.1);
    @media screen and (min-width: $tablet-medium) {
      padding: 25px 30px;
    }

    .wrap-btn {
      @media screen and (min-width: $tablet-medium) {
        display: flex;
      }

      .btn {
        display: block;
        flex: 1;
        margin-bottom: 10px;
        @media screen and (min-width: $tablet-medium) {
          max-width: 200px;
          margin-right: 10px;
          margin-bottom: 0;
        }

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
