.shopping-cart {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small) {
    margin-bottom: 100px;
  }

  &__row {
    @media screen and (min-width: $desktop-small) {
      display: flex;
      flex-wrap: wrap;
    }
  }

  .table-product {
    margin-bottom: 30px;
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 300px);
      padding-right: 30px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1200px) {
      width: calc(100% - 480px);
      padding-right: 50px;
    }
  }

  .card-totals {
    @media screen and (min-width: $desktop-small) {
      width: 300px;
      height: 100%;
    }
    @media screen and (min-width: 1200px) {
      width: 480px;
    }
  }

  .wrap-btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }
}

.card-totals {
  .title-card {
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
    padding-bottom: 24px;
    border-bottom: 1px solid var(--line);
  }
}

.table-product {
  .column-labels {
    display: flex;
    flex-direction: row;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--line);

    label {
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;
      text-transform: uppercase;
      @media screen and (max-width: $tablet-medium - 1){
        &:not(.product-details) {
          display: none;
        }
      }
    }

    .product-image, .product-removal {
      text-indent: -9999px;
    }

    .product-details {
      padding-left: 0;
      width: 43%;
    }
  }

  .product {
    &__item {
      display: flex;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap;
      padding: 15px 0;
      border-bottom: 1px solid var(--line);
      @media screen and (max-width: $tablet-medium - 1) {
        position: relative;
        padding-left: 18%;
        .product-image {
          position: absolute;
          top: 18px;
          left: 0;
          width: 15%;
        }
        .product-details,
        .product-price {
          width: 100%;
          padding: 0;
          text-align: left;
        }
        .product-quantity, .product-removal {
          width: 50%;
        }
        .product-quantity {
          .qty-input {
            margin-left: 0;
          }
        }
        .product-price {
          margin-bottom: 5px;
        }
        .product-line-price {
          display: none;
        }
      }
    }
  }

  .product-image {
    width: 8%;

    img {
      border-radius: 3px;
      overflow: hidden;
      @media screen and (min-width: 1200px) {
        max-width: 75px;
      }
    }
  }

  .product-details {
    width: 35%;
    padding: 0 10px;
  }

  .product-price,
  .product-quantity,
  .product-line-price {
    text-align: center;
  }

  .product-price {
    width: 12%;
  }

  .product-quantity {
    width: 20%;
  }

  .product-removal {
    width: 10%;

    .remove-product {
      display: block;
      margin-left: auto;
      svg:hover {
        path {
          stroke: var(--dark);
        }
      }
    }
  }

  .product-line-price {
    width: 15%;
  }

  .qty-input {
    width: 120px;
    margin: 0 auto;

    .product-count {
      width: 35px;
    }

    .product-qty {
      width: 50px;
    }
  }
}

.card-totals {
  padding: 30px 20px;
  border: 1px solid var(--dark);
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  @media screen and (min-width: $tablet-medium) {
    padding: 30px;
  }
}

.totals {
  .totals-item {
    padding: 25px 0;
    border-bottom: 1px solid var(--line);

    &:last-child {
      border-bottom: 0;
    }
  }

  .totals-value {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    span {
      color: var(--gray);
    }

    &.totals-all {
      font-weight: 600;

      span {
        text-transform: uppercase;
        font-weight: 400;
      }
    }
  }

  .title-get {
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    margin-bottom: 7px;
  }

  .content {
    font-size: 14px;
    line-height: 20px;
    color: var(--gray);
    margin-bottom: 20px;

    a {
      text-decoration: underline;
      color: var(--dark);
    }
  }

  .panel-message {
    border-radius: 7px;
    padding: 15px 20px;
    text-align: center;

    &.success {
      background: var(--green-light);
      color: var(--green);
    }

    h5 {
      font-weight: 600;
      font-size: 14px;
      line-height: 22px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }

  .btn {
    display: block;
  }

  .calculate-content {
    margin-bottom: 15px;
  }

  .pays-list {
    display: flex;
    justify-content: center;
    margin-top: 30px;

    li {
      max-width: 40px;
      margin-right: 7px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .group-address {
    display: none;
  }

  .calculator-ship {
    margin-bottom: 20px;
  }

  .calculate-content {
    font-size: 14px;
    line-height: 20px;
    color: var(--dark);
    display: none;

    p, li {
      margin-bottom: 10px;
    }

    ul {
      list-style: initial;
      padding-left: 15px;
    }
  }
}
