.slick-arrow {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  border: 1px solid var(--white);
  background-color: var(--white);
  font-size: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: all .3s;
  z-index: 1;
  @media screen and (min-width: $tablet-medium){
    width: 50px;
    height: 50px;
  }
  &:hover {
    box-shadow: 0 5px 12px rgba(0, 0, 0, 0.07);
    border: 1px solid var(--line-2);
  }
  &::before {
    display: block;
    @include e-comstore;
    font-size: 15px;
    color: var(--gray-2);
    @media screen and (min-width: $tablet-medium){
      font-size: 18px;
    }
  }
  &.slick-disabled {
    &::before {
      color: var(--line-2);
    }
  }
  &.slick-prev {
    left: -15px;
    @media screen and (min-width: $tablet-medium){
      left: -25px;
    }
    &::before {
      content: '\e90c';
    }
  }
  &.slick-next {
    right: -15px;
    @media screen and (min-width: $tablet-medium){
      right: -25px;
    }
    &::before {
      content: '\e90b';
    }
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  li {
    border-radius: 50%;
    width: 9px;
    height: 9px;
    background: rgba(32, 33, 36, 0.25);
    margin: 0 5px;
    &.slick-active {
      background-color: var(--dark);
    }
    button {
      display: none;
    }
  }
}

.group-arrow {
  .slick-arrow {
    @media screen and (min-width: $tablet-medium){
      border-radius: 3px;
      border-color: transparent;
      width: 55px;
      height: 25px;
      bottom: 0;
      top: auto;
      transform: none;
      background-color: var(--white-2);
      &::before {
        font-size: 10px;
      }
      &.slick-disabled {
        background-color: rgba(250, 250, 250, 0.01);
      }
      &.slick-prev {
        left: 0;
      }
      &.slick-next {
        right: 0;
      }
    }
  }
}
