.footer {
  &__main {
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.05);
    padding: 20px 0 30px;
    @media screen and (min-width: $desktop-small){
      padding: 64px 0;
    }

    .row {
      @media screen and (min-width: $tablet-medium){
        .col {
          flex: 1;
          padding: 0 15px;
        }
      }
      .col {
        @media screen and (max-width: $tablet-medium - 1){
          &:last-child {
            margin-top: 25px;
          }
        }
      }
    }

    ul {
      li {
        font-weight: 600;
        font-size: 21px;
        line-height: 27px;
        color: var(--dark);
        @media screen and (max-width: $tablet-medium - 1){
          font-size: 18px;
          line-height: 22px;
          &.has-child {
            position: relative;
            padding: 25px 0;
            border-bottom: 1px solid #EBF0F7;
            &.active {
              .icon {
                &:after {
                  transform: rotate(90deg);
                }
              }
            }
            .icon {
              position: absolute;
              top: 27px;
              right: 0;
              display: block;
              width: 15px;
              height: 15px;

              &:before,
              &:after {
                content: '';
                position: absolute;
                background: var(--dark);
                transition: 150ms all ease-out;
              }

              &:before {
                top: 7px;
                left: 0;
                width: 100%;
                height: 1px;
              }

              &:after {
                top: 0;
                left: 7px;
                height: 100%;
                width: 1px;
              }
            }
          }
        }

        ul {
          width: 100%;
          margin-top: 15px;
          padding-left: 15px;
          @media screen and (min-width: $tablet-medium){
            margin-top: 20px;
            padding-left: 0;
            display: block!important;
          }
          @media screen and (max-width: $tablet-medium){
            display: none;
          }
        }

        li {
          font-weight: 400;
          font-size: 16px;
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 0;
          }

          a {
            position: relative;
            display: inline-block;
            &::before {
              content: '';
              position: absolute;
              bottom: 0;
              left: 0;
              right: 100%;
              height: 1px;
              background-color: var(--dark);
              transition: all .2s ease-in-out;
            }
          }
          a:hover::before {
            right: 0;
          }
        }
      }
    }

    .follow-us {
      margin-top: 6px;
      margin-bottom: 20px;
      font-size: 14px;
      line-height: 20px;
      color: var(--gray);
      @media screen and (min-width: $tablet-medium){
        margin-top: 22px;
      }
    }
  }

  .social {
    a {
      color: var(--gray);
    }
  }

  &__bottom {

    .wrap-content {
      padding: 15px 0;
      box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.05);
      color: var(--gray);
      @media screen and (min-width: $tablet-medium){
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      @media screen and (max-width: $tablet-medium - 1){
        .copyright {
          font-size: 14px;
          margin-bottom: 10px;
          text-align: center;
        }
        .pays-list {
          justify-content: center;
        }
      }
    }

    .pays-list {
      display: flex;

      li {
        margin-right: 8px;
        max-width: 47px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
