.banner-single {
  margin-bottom: 50px;
  &__content {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    min-height: 250px;
    @include backgroundSize(cover);
  }

  .content-text {
    @media screen and (min-width: $tablet-medium){
      width: 50%;
    }
    @media screen and (max-width: $tablet-medium - 1){
      padding: 30px 20px;
    }
  }

  .sub-title {
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: var(--gray);
    margin-bottom: 5px;
    @media screen and (min-width: $tablet-medium){
      font-size: 21px;
      margin-bottom: 11px;
    }
  }

  .title {
    font-size: 40px;
    line-height: 45px;
    font-weight: bold;
    @media screen and (min-width: $tablet-medium){
      font-size: 50px;
      line-height: 50px;
    }
  }
}
