.banner {
  margin-bottom: 40px;
  @media screen and (min-width: $tablet-medium){
    background: linear-gradient(90deg, rgba(248, 73, 27, 0.15) 0%, rgba(220, 24, 96, 0.15) 99.64%);
    margin-bottom: 50px;
  }
  .container {
    display: flex;
    align-items: center;
    @media screen and (max-width: $tablet-medium - 1){
      flex-direction: column-reverse;
    }
  }
  .content,.image {
    @media screen and (min-width: $tablet-medium){
      width: 50%;
    }
  }
  .image {
    align-self: flex-end;
    @media screen and (min-width: $tablet-medium){
      padding-top: 60px;
    }
    @media screen and (max-width: $tablet-medium - 1){
      margin: 0 -15px;
      background: linear-gradient(90deg, rgba(248, 73, 27, 0.15) 0%, rgba(220, 24, 96, 0.15) 99.64%);
    }
  }
  .content {
    font-weight: 300;
    color: var(--black);
    padding: 20px 0 0;
    @media screen and (max-width: $tablet-medium - 1){
      text-align: center;
    }
    @media screen and (min-width: $tablet-medium){
      font-size: 18px;
      line-height: 25px;
      padding: 50px 0;
    }
    @media screen and (min-width: $desktop-small){
      font-size: 21px;
      line-height: 21px;
    }
    .sub-title {
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: var(--dark);
      margin-bottom: 5px;
      @media screen and (min-width: $desktop-small){
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 10px;
      }
    }
    .title {
      font-weight: bold;
      color: var(--dark);
      margin-bottom: 12px;
    }
    .btn {
      margin-top: 25px;
      @media screen and (min-width: $desktop-small){
        margin-top: 45px;
      }
    }
  }
}
