.filter-sort {
  position: relative;
  margin-bottom: 50px;
  z-index: 20;

  @media screen and (max-width: $tablet-medium - 1){
    .sort-group {
      width: 100%;
    }
    .select2-container--select-md {
      width: 100%!important;
    }
  }

  .row-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 15px;
    &:not(.space-between) {
      @media screen and (min-width: $tablet-medium){
        padding-right: 170px;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    .title {
      font-size: 14px;
      line-height: 14px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: var(--gray);
      margin-bottom: 15px;
    }
  }

  .list-filter {
    display: flex;
    flex-wrap: wrap;
    @media screen and (min-width: $tablet-medium){
      margin-right: 15px;
    }

    .checkbox-dropdown {
      margin-right: 15px;
      margin-bottom: 15px;
      @media screen and (max-width: $tablet-medium - 1){
        width: 100%;
        margin-right: 0;
        max-width: none;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .action-view {
    position: absolute;
    top: 42px;
    right: 0;
    @media screen and (max-width: $tablet-medium - 1){
      display: none;
    }
  }

  .wrapper-action {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 18px;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--line);
    margin-bottom: 20px;

    .btn-clear {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

.action-view {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex: 1;

  .view-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    margin-right: 20px;

    circle {
      fill: var(--l-gray);
    }

    rect {
      stroke: var(--l-gray);
    }

    &.active, &:hover {
      circle {
        fill: var(--dark);
      }

      rect {
        stroke: var(--dark);
      }
    }

    &.view-list {
      margin-right: 0;

      &.active, &:hover {
        rect {
          fill: var(--dark);
        }
      }
    }
  }
}

.list-tag {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex: 1;

  .tag {
    position: relative;
    font-size: 14px;
    line-height: 14px;
    border-radius: 20px;
    background-color: var(--gray-3);
    padding: 7px 30px 7px 15px;
    margin: 5px 10px 5px 0;
    cursor: pointer;
    transition-duration: .3s;
    @media screen and (max-width: $tablet-medium - 1){
      min-width: 120px;
    }

    &:hover {
      color: var(--white);
      background-color: var(--dark);
    }

    &:last-child {
      margin-right: 0;
    }

    .btn-close {
      position: absolute;
      right: 10px;
      top: 7px;

      &::before {
        content: '\e90d';
        @include e-comstore;
      }
    }
  }
}

.clear-all {
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin-left: 20px;
  text-decoration: underline;
  &:hover {
    text-decoration: none;
  }
}
