.checkout-order {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small) {
    margin-bottom: 100px;
  }

  &__row {
    @media screen and (min-width: $desktop-small) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
    }
  }

  .checkout-info {
    margin-bottom: 40px;
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 470px);
      padding-right: 50px;
      margin-bottom: 0;
    }
  }

  .checkout-your-order {
    @media screen and (min-width: $desktop-small) {
      width: 470px;
      flex-shrink: 0;
    }
  }
}

.checkout-info {
  .mb-30 {
    margin-bottom: 20px;
    @media screen and (min-width: $tablet-medium){
      margin-bottom: 30px;
    }
  }

  .mb-50 {
    margin-bottom: 40px;
    @media screen and (min-width: $tablet-medium){
      margin-bottom: 50px;
    }
  }

  .panel-return {
    padding: 18px 20px;
    background-color: var(--gray-4);
    border-radius: 7px;

    p {
      margin-bottom: 0;
    }

    a {
      color: var(--dark);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .title {
    font-weight: 600;
    font-size: 21px;
    line-height: 21px;
    margin-bottom: 9px;
  }

  p {
    font-size: 14px;
    line-height: 18px;
    color: var(--gray);
    margin-bottom: 20px;
    @media screen and (min-width: $tablet-medium){
      line-height: 14px;
    }
  }

  .box-detail {
    position: relative;
    margin-top: 25px;
    border: 1px solid var(--line-2);
    border-radius: 7px;
    padding: 20px 50px 20px 20px;

    .title-address {
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      color: var(--dark);
      margin-bottom: 14px;
    }

    .text {
      font-weight: 500;
    }

    .link-change {
      color: var(--blue-2);
      margin-top: 10px;
      display: inline-block;
      @media screen and (min-width: $tablet-medium) {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 20px;
        margin-top: 0;
      }

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .shopping-unit {
    .express-item {
      padding: 20px;
      border-radius: 5px;
      border: 1px solid var(--line-2);
      margin-bottom: 20px;

      &.active {
        border: 3px solid var(--green);
        padding: 18px;
      }
    }

    .content {
      margin-top: 4px;
      padding-left: 34px;

      p {
        margin-bottom: 12px;
      }

      .wrap-btn {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    .btn-free {
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;
      display: inline-block;
      padding: 5px 10px;
      color: var(--white);
      background-color: var(--blue-2);
      border: 1px solid;
      border-radius: 40px;
      min-width: 85px;
      text-align: center;
      transition: all .3s ease;

      &:hover {
        background-color: var(--white);
        color: var(--blue-2);
      }
    }

    .link {
      font-weight: 500;
      font-size: 14px;
      line-height: 14px;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .card-note {
    margin-top: 25px;
    border: 1px solid var(--line-2);
    box-shadow: 0 2px 9px rgba(0, 0, 0, 0.07);
    border-radius: 7px;
    padding: 25px 15px;
    @media screen and (min-width: $tablet-medium){
      padding: 25px 20px;
    }

    .title-pay {
      font-weight: 600;
      font-size: 18px;
      line-height: 18px;
      color: var(--dark);
      margin-bottom: 10px;
    }

    .wrap-btn {
      border-top: 1px solid var(--line);
      padding-top: 30px;
    }
  }

  .row-form {
    margin-top: 20px;

    .form-group {
      margin-bottom: 15px;
      @media screen and (min-width: $tablet-medium) {
        width: calc(50% - 10px);
        margin-right: 20px;
        margin-bottom: 0;
      }

      &.select-custom {
        @media screen and (min-width: $tablet-medium) {
          margin-bottom: 0;
        }
      }

      &:nth-child(2n), &:last-child {
        margin-right: 0;
      }
    }
  }
}

.checkout-your-order {
  border: 1px solid var(--dark);
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  height: 100%;
  padding: 30px 20px;
  @media screen and (min-width: $tablet-medium){
    padding: 30px;
  }

  .title-card {
    font-weight: bold;
    font-size: 24px;
    line-height: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid var(--line);
  }

  .shopping-item {
    display: flex;
    align-items: center;
    padding: 12px 0;
    border-bottom: 1px solid var(--line);
    @media screen and (max-width: $tablet-medium - 1){
      font-size: 14px;
    }

    .image {
      max-width: 42px;
      flex-shrink: 0;
      margin-right: 12px;
    }

    .info {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    .product-name {
      span {
        font-size: 14px;
        line-height: 14px;
        color: var(--gray);
        display: block;
        margin-top: 5px;
      }
    }
  }

  .row-order {
    padding: 24px 0;
    border-bottom: 1px solid var(--line);

    p {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      text-transform: capitalize;

      span {
        color: var(--gray);
      }
    }

    .form-code {
      position: relative;
      margin-bottom: 14px;

      .btn-apply {
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        background: #A9A9A9;
        border-radius: 5px;
        border: 1px solid #A9A9A9;
        font-weight: 500;
        transition: all .3s ease;

        &:hover {
          background-color: rgba(169, 169, 169, 0.8);
          border-color: rgba(169, 169, 169, 0.8);
        }
      }
    }

    .view-promo {
      font-weight: 600;
      font-size: 14px;
      line-height: 14px;
      color: var(--blue-2);
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .total {
    padding-top: 30px;

    p {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-weight: 600;
      font-size: 21px;
      line-height: 21px;
    }
  }
}
