[class*='icon-flag-'] {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  margin-top: -2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  @include backgroundSize(cover);
}

.icon-flag-us {
  background-image: url('../images/flags/flag-united-states-of-america.png');
}

.icon-flag-fr {
  background-image: url('../images/flags/flag-france.png');
}

.icon-flag-spa {
  background-image: url('../images/flags/flag-spain.png');
}
