.product-detail {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }

  &:not(.product-hot) {

    .main-detail {
      padding-bottom: 60px;
      border-bottom: 1px solid var(--line);
      @media screen and (min-width: $desktop-small){
        padding-bottom: 100px;
      }
    }
  }
}

.main-detail {
  @media screen and (min-width: $desktop-small){
    display: flex;
    &__content {
      width: 45%;
      padding-left: 45px;
    }

    &__slider {
      width: 55%;
      height: 100%;
    }
  }

  &__slider {
    display: flex;
    @media screen and (max-width: $desktop-small - 1){
      margin-bottom: 30px;
    }
    @media screen and (max-width: $tablet-medium - 1){
      flex-direction: column-reverse;
    }
  }

  .slider-for {
    margin-bottom: 15px;
    @media screen and (min-width: $tablet-medium){
      width: calc(100% - 115px);
      padding-left: 8px;
      margin-bottom: 0;
    }
  }

  .slider-nav {
    @media screen and (min-width: $tablet-medium){
      flex: 1;
      width: 115px;
      overflow: hidden;
      height: calc(100% - 35px);
    }

    .slick-list {
      @media screen and (max-width: $tablet-medium - 1){
        margin: 0 -2px;
      }
    }

    img {
      width: 100%;
      object-fit: cover;
      @media screen and (min-width: $tablet-medium){
        max-height: 135px;
      }
      @media screen and (min-width: $desktop-small){
        max-height: 145px;
      }
    }
  }

  .group-arrow {
    position: absolute;
    bottom: 50%;
    height: 40px;
    width: 100%;
    right: 0;
    transform: translateY(50%);
    @media screen and (min-width: $tablet-medium){
      bottom: 0;
      height: 25px;
      width: 115px;
      right: 100%;
      transform: none;
    }
  }

  .product-name {
    font-weight: 500;
    margin-bottom: 5px;
  }

  .price {
    font-size: 24px;
    line-height: 24px;
    color: var(--dark);
    margin-bottom: 15px;
  }

  .price-old {
    font-size: 18px;
    line-height: 18px;
    color: var(--l-gray);
    text-decoration: line-through;
  }

  .product-image {
    img {
      max-height: 420px;
      width: 100%;
      object-fit: cover;
      @media screen and (min-width: $tablet-medium){
        max-height: 800px;
      }
    }
  }

  .thumbnail-image {
    margin-right: 4px;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 5px;
    @media screen and (min-width: $tablet-medium){
      margin-bottom: 9px;
      margin-right: 0;
    }

    &.slick-current {
      border: 1px solid var(--black);
    }
  }
}

.info-product {
  padding: 25px 0 20px;
  border-top: 1px solid var(--line);

  .description {
    border-bottom: 1px solid var(--line);
    padding-bottom: 5px;
    margin-bottom: 25px;
  }

  p {
    margin-bottom: 18px;
  }

  ul {
    list-style: initial;
    margin-bottom: 7px;
    padding-left: 15px;

    li {
      margin-bottom: 7px;
    }
  }

  .btn {
    text-align: center;
    margin-bottom: 15px;
    display: block;
    @media screen and (min-width: $desktop-small){
      max-width: 450px;
      margin-bottom: 20px;
    }
  }
}

.product-review {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 25px;

  .list-action {
    display: flex;

    .item {
      margin-right: 30px;
      svg path {
        transition: all .3s ease;
      }
      &:hover {
        path {
          stroke: var(--dark);
        }
      }

      &:last-child {
        margin-right: 0;
        &:hover {
          path {
            fill: var(--dark);
          }
        }
      }
    }
  }
}

.reviews {
  font-size: 14px;
  line-height: 14px;
  color: var(--gray);
  margin-bottom: 25px;

  &.inline {
    display: flex;
    align-items: center;
    margin-bottom: 0;
  }

  .list-star {
    margin-right: 5px;
  }

  .line {
    width: 1px;
    height: 14px;
    background-color: var(--line-3);
    margin: 0 10px;
  }

  .number {
    font-size: 35px;
    line-height: 24px;
    color: var(--dark);
    margin-bottom: 10px;

    span {
      font-size: 24px;
    }
  }

  &__star {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    .reviews-text {
      margin-bottom: 0;
      margin-left: 12px;
    }
  }
}

.reviews-filter {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  .filter-item {
    width: calc(33.33% - 6.667px);
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  .select2 {
    width: 100% !important;
  }
}

.list-star {
  .icon-star {
    color: var(--line-2);

    &.active {
      color: var(--yellow);
    }
  }
}

.reviews-comment {
  &__list {
    margin-bottom: 5px;

    &__item {
      padding-top: 30px;
      padding-bottom: 20px;
      border-top: 1px solid var(--line);

      &:last-child {
        border-bottom: 1px solid var(--line);
      }
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }

  &__main {
    .title-comment {
      font-weight: 600;
      margin-bottom: 7px;
    }
  }
}

.reviews-rating__actions {
  display: flex;

  .action-item {
    position: relative;
    font-size: 14px;
    line-height: 24px;
    color: var(--gray);
    padding: 0 12px;

    &::after {
      content: '';
      position: absolute;
      top: 5px;
      right: 0;
      width: 1px;
      height: 15px;
      background-color: var(--line-2);
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;

      &::after {
        display: none;
      }
    }
  }

  .icon-like {
    color: var(--line-2);
    margin-right: 5px;

    &.liked {
      color: var(--red);
    }
  }
}

.list-media-image {
  display: flex;
  margin-bottom: 14px;

  .item {
    width: 93px;
    border-radius: 3px;
    margin-right: 8px;
    overflow: hidden;
  }
}

.date-time {
  font-size: 14px;
  line-height: 24px;
  color: var(--gray);
}

.user-account {
  display: flex;

  .avatar {
    width: 45px;
    height: 45px;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 15px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .name {
    font-weight: 500;
    color: var(--dark);
  }

  .list-star {
    font-size: 13px;
  }
}

.list-option {
  display: flex;
  flex-wrap: wrap;
  &.opt-column {
    flex-direction: column;
  }

  &.list-color {
    li {
      width: 37px;
      height: 37px;
      border-radius: 50%;
      margin-top: 10px;
      margin-bottom: 10px;
      margin-right: 15px;
      transition: all .3s ease;
      &:hover {
        transform: scale(1.1);
      }

      &::before {
        content: '';
        position: absolute;
        left: -4px;
        right: -4px;
        top: -4px;
        bottom: -4px;
        border-radius: 50%;
        border: 1px solid var(--line);
      }

      &.active, &:hover {
        &::before {
          border-color: var(--dark);
        }
      }
    }
  }

  &.list-size {
    li {
      width: 45px;
      height: 45px;
      margin-top: 5px;
      margin-bottom: 5px;
      border-radius: 3px;
      border: 1px solid var(--line);

      &.active, &:hover {
        border-color: var(--dark);
      }
    }
  }

  li {
    position: relative;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    &.active, &:hover {
      box-shadow: 0 2px 7px rgba(0, 0, 0, 0.07);
    }
  }
}

.options {
  margin-bottom: 29px;

  label {
    font-size: 14px;
    line-height: 14px;
    display: inline-block;
    color: var(--gray);
    text-transform: uppercase;
    margin-bottom: 12px;
  }

  .small {
    font-size: 14px;
    line-height: 24px;
    color: var(--gray);
    margin-bottom: 0;

    a {
      color: var(--dark);
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

.quality {
  .quality-group {
    display: flex;
    align-items: center;
  }

  .small {
    margin-left: 15px;
  }
}
