.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 1px 2px rgba(0, 0, 24, 0.1);
  background-color: var(--white);
  transition: top 0.3s ease;
  z-index: 9999;

  &__top {
    font-size: 14px;
    line-height: 18px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
    @media screen and (max-width: $desktop-small - 1) {
      display: none;
    }

    &__left,&__right {
      display: flex;
      align-items: center;
    }
  }

  &__main {
    position: relative;
    @media screen and (max-width: $desktop-small - 1) {
      padding: 12px 0;
    }

    .menu-ul, .list-item {
      display: flex;
      align-items: center;

      li {
        margin-right: 30px;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    .list-item {
      @media screen and (max-width: $desktop-small - 1){
        margin-top: 4px;
      }
      li {
        @media screen and (max-width: $desktop-small - 1) {
          margin-right: 15px;
          &:nth-child(2), &:last-child {
            display: none;
          }
          &:nth-last-child(2) {
            margin-right: 0;
          }
        }

        &.search-item {
          z-index: 5;
        }

        #svg-close {
          visibility: hidden;
          height: 0;
          transition: all .35s;
        }

        &.has-product {
          .cart-open {
            position: relative;
            &::after {
              content: '';
              top: 2px;
              right: 3px;
              position: absolute;
              width: 8px;
              height: 8px;
              background-color: var(--dark);
              border-radius: 50%;
            }
          }
          .cart-number {
            position: absolute;
            bottom: 3px;
            left: calc(50% - 1px);
            font-weight: bold;
            font-size: 10px;
            line-height: 10px;
            transform: translateX(-50%);
          }
        }
      }
    }

    .logo {
      @media screen and (max-width: $desktop-small - 1) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 135px;
      }
    }

    .main-menu {
      @media screen and (max-width: $desktop-small - 1) {
        position: fixed;
        top: 60px;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: var(--white);
        padding-bottom: 50px;
        transform: translateX(-100%);
        transition: all .5s ease;
        border-top: 1px solid var(--line);
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 100vh;
        &.open {
          transform: translateX(0);
        }
      }

      .social {
        @media screen and (min-width: $desktop-small) {
          display: none;
        }
        @media screen and (max-width: $desktop-small - 1) {
          padding-top: 30px;
          padding-left: 15px;
          padding-right: 15px;
          li {
            font-size: 20px;
          }
        }
      }
    }

    .menu-ul {
      li {
        font-weight: 500;
      }

      > li {
        @media screen and (min-width: $desktop-small){
          &.active, &:hover {
            > a {
              &::before {
                right: 0;
              }
            }
          }
        }

        > a {
          position: relative;
          display: block;
          padding-top: 30px;
          padding-bottom: 30px;
          &::before {
            content: '';
            position: absolute;
            bottom: 30px;
            left: 0;
            right: 100%;
            height: 1px;
            background-color: var(--dark);
            transition: all .2s ease-in-out;
          }
        }
      }

      @media screen and (max-width: $desktop-small - 1) {
        display: block;
        padding: 15px 0;
        border-bottom: 1px solid #F0F0F0;
        > li {
          font-size: 16px;
          margin-right: 0;

          > a {
            padding: 7px 15px;

            &::after {
              display: none;
            }
          }
        }
      }
    }
  }

  .container-fluid {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.menu-control {
  padding: 15px 0;
  display: block;
  border-bottom: 1px solid #F0F0F0;

  li {
    a {
      padding: 7px 15px;
    }
  }
  @media screen and (min-width: $desktop-small){
    display: none;
  }
}

.toggle-button {
  position: relative;
  width: 25px;
  height: 20px;
  @media screen and (min-width: $desktop-small) {
    display: none;
  }

  &.open {
    .menu-bar-top {
      transform: rotate(45deg) translate(4px, 7px);
      transition: .5s;
    }

    .menu-bar-middle {
      transform: translate(230px);
      transition: .1s ease-in;
      opacity: 0;
    }

    .menu-bar-bottom {
      transform: rotate(-45deg) translate(5px, -9px);
      transition: .5s;
    }
  }

  .menu-bar {
    position: absolute;
    border-radius: 2px;
    width: 100%;
    transition: .5s;
  }

  .menu-bar-top {
    border: 2px solid #555;
    border-bottom: 0;
    top: 0;
  }

  .menu-bar-middle {
    height: 2px;
    background-color: #555;
    margin-top: 7px;
    margin-bottom: 7px;
    top: 2px;
  }

  .menu-bar-bottom {
    border: 2px solid #555;
    border-top: 0;
    top: 18px;
  }
}

.main-menu {
  > li {
    &:hover, &.active {
      color: var(--dark);
    }
  }

  .sub-menu {
    @media screen and (min-width: $desktop-small) {
      position: absolute;
      left: 0;
      top: 100%;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      min-width: 210px;
      transition: .3s ease-in-out;
      z-index: 1;
    }

    > li {
      display: block;
      padding: 7px 15px;
      color: var(--dark);
      font-weight: 400;
      @media screen and (min-width: $desktop-small){
        padding: 0;
        margin: 20px 15px;
        border-bottom: 0;
      }

      &.back-menu {
        position: relative;
        font-size: 16px;
        font-weight: 700;
        display: flex;
        align-items: center;
        margin-right: 0;
        box-shadow: 0 1px 0 #EBEBEB;
        margin-bottom: 15px;

        &::before {
          content: '\e915';
          display: block;
          @include e-comstore;
          font-size: 18px;
          margin-right: 5px;
          margin-top: -2px;
          margin-left: -2px;
        }

        @media screen and (min-width: $desktop-small) {
          display: none;
        }
      }

      > a {
        position: relative;
        @media screen and (min-width: $desktop-small) {
          &:hover {
            &::before {
              right: 0;
            }
          }
          &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 100%;
            height: 1px;
            background-color: var(--dark);
            transition: all .2s ease-in-out;
          }
        }
      }
    }
  }

  .sub-menu {
    background-color: var(--white);
    @media screen and (min-width: $desktop-small) {
      padding-left: 0;
      box-shadow: 0 0 7px rgba(0, 0, 0, 0.07), 0 20px 30px rgba(0, 0, 0, 0.07);
      transform: translateY(30px);
    }
    @media screen and (max-width: $desktop-small - 1) {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform: translateX(100%);
      transition: all .4s ease;
      z-index: 10;
    }
  }

  li {
    position: relative;

    .arrow-menu {
      position: absolute;
      top: 12px;
      right: 15px;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;

      &::before {
        content: '\e911';
        @include e-comstore;
        display: block;
        transition: all .3s;
      }

      @media screen and (min-width: $desktop-small) {
        display: none;
      }
    }

    &.has-sub {
      position: relative;
      @media screen and (min-width: $desktop-small) {
        padding-right: 20px;
        &::after {
          content: '\e913';
          @include e-comstore;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 0;
        }
      }

      &:hover {
        @media screen and (min-width: $desktop-small) {
          > .sub-menu {
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
            pointer-events: auto;
          }
        }
      }

      &.show {
        @media screen and (max-width: $desktop-small - 1) {
          > .sub-menu {
            transform: translateX(0);
          }
        }
      }

      .has-sub {
        @media screen and (min-width: $desktop-small) {
          &::after {
            right: 15px;
            transform: translateY(-50%) rotate(
                            -90deg
            );
          }
        }
      }
    }
  }
}

html {
  &.open-menu {
    overflow: hidden;
    #header {
      top: 0!important;
    }
  }

  &.open-search {
    overflow: hidden;

    .search__overlay {
      visibility: visible;
    }

    .tooltip {
      &__content {
        display: none;
      }
    }

    .list-item li {
      #svg-search {
        visibility: hidden;
        height: 0;
      }

      #svg-close {
        visibility: visible;
        height: auto;
      }
    }

    .header-search {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);

      &__results {
        display: block;
      }
    }
  }
}

.header-search {
  position: absolute;
  visibility: hidden;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  min-height: 100%;
  width: 100%;
  background-color: var(--white);
  transform: translateY(-100%);
  opacity: 0;
  transition: all .35s cubic-bezier(.7, 0, .3, 1);
  z-index: 2;

  &__form {
    flex: 1 0 auto;
    max-width: 100%;
  }

  &__results {
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    display: none;
    background-color: var(--white);
    padding-top: 30px;
    padding-bottom: 30px;
    border-top: 1px solid var(--line);
    @media screen and (max-width: $tablet-medium - 1) {
      max-height: 600px;
      overflow-y: auto;
    }

    .search__overlay {
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      visibility: hidden;
      transition: visibility 0s .3s;
      z-index: 1;
    }

    .product-list.grid {
      border-bottom: 1px solid var(--line);
      margin-bottom: 30px;
      @media screen and (max-width: $tablet-medium - 1) {
        display: flex;
        flex-wrap: wrap;
      }

      .product__item {
        margin-bottom: 30px;
        @media screen and (max-width: $tablet-medium - 1) {
          width: calc(50% - 10px);
          margin-right: 20px;
          &:nth-child(2n), &:last-child {
            margin-right: 0;
          }
        }
      }
    }

    .product__item {
      .image {
        height: 150px;
        @media screen and (min-width: $tablet-medium) {
          height: 350px;
        }
      }
    }

    .wrap-btn {
      text-align: center;
    }

    .loader {
      .product__item {
        .image, .title, .price {
          font-size: 0;
          background: linear-gradient(to right, var(--gray-3) 8%, var(--white-2) 18%, var(--gray-3) 33%);
          @include animated-background;
        }

        .image {
          &::before, &::after {
            display: none;
          }
        }

        .price {
          max-width: 155px;
        }
      }
    }
  }

  .form-group {
    position: relative;

    .icon-search, .close {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .icon-search {
      left: 0;
      font-size: 22px;
    }

    .close {
      right: 0;
      font-size: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 20px;
      height: 20px;

      path {
        transition: all .3s ease;
      }

      &:hover {
        path {
          stroke: var(--dark);
        }
      }
    }

    .form-control {
      width: 100%;
      border: 0;
      font-size: 20px;
      line-height: 20px;
      padding: 15px 30px 15px 50px;
      @media screen and (min-width: $tablet-medium) {
        font-size: 24px;
        line-height: 24px;
      }
      @include placeholder {
        color: var(--l-gray);
      }
    }
  }
}

