.our-blog {
  margin-bottom: 60px;
  @media screen and (min-width: $desktop-small){
    margin-bottom: 100px;
  }

  .list-news {
    @media screen and (min-width: $tablet-medium){
      display: flex;
      flex-wrap: wrap;
    }

    .title a:hover {
      text-decoration: underline;
    }

    .news__item {
      margin-bottom: 30px;
      @media screen and (min-width: $tablet-medium){
        width: calc(33.33% - 13.33px);
        margin-right: 20px;
        margin-bottom: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
