.tabs {
  padding-top: 30px;
  border-top: 1px solid var(--line);

  .tabs-nav {
    display: flex;
    margin-bottom: 20px;
    @media screen and (max-width: $tablet-medium - 1){
      display: none;
    }

    li {
      font-weight: 500;
      font-size: 16px;
      line-height: 18px;
      color: var(--gray);
      margin-right: 30px;
      text-transform: uppercase;

      &:last-child {
        margin-right: 0;
      }

      &.active, &:hover {
        color: var(--dark);
      }

      &.active a::after {
        right: 0;
      }
    }
  }
}

.tab-content {
  @media screen and (min-width: $tablet-medium){
    display: none;
    .collapse-title {
      display: none;
    }
  }

  @media screen and (max-width: $tablet-medium - 1){
    .collapse-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: bold;
      padding: 10px 0;
      &.active {
        &:after {
          transform: rotate(-180deg);
        }
      }
      &:after {
        content: '\e913';
        font-size: 20px;
        display: block;
        @include e-comstore;
        transition: all .3s;
      }
    }
    .collapse-content {
      display: none;
    }
  }

  p {
    margin-bottom: 15px;
  }

  ul {
    li {
      margin-bottom: 7px;
    }
  }

  .link-more {
    margin-top: 10px;
    margin-bottom: 20px;
    @media screen and (min-width: $tablet-medium){
      margin-top: 20px;
    }
  }
}
